var React = require('react');
var ReactDOM = require('react-dom');
var Operation = require('./opeartion/operation-main');

var imageLoader = document.getElementById('imageLoader');
imageLoader.addEventListener('change', handleImage, false);

var canvas = new fabric.Canvas('imageCanvas',{backgroundColor: 'rgb(240,240,240)'});

var bgImg = new Image();
bgImg.src = 'data:image/jpeg;base64,';
var bgImgInstance = new fabric.Image(bgImg, {selectable: false});
canvas.add(bgImgInstance);
canvas.moveTo(bgImgInstance, 0);

var lineHeight1 = new fabric.Text('北京超凡志成知识产权代理事务所', {
  fontFamily: 'SimSun',
  fill: "#333",
  fontWeight: '100',
  fontSize: parseInt('14'),
  editable: true
});
canvas.add(lineHeight1);
canvas.moveTo(lineHeight1, 1);

function handleImage(e){
  var reader = new FileReader();
  reader.onload = function(event){
    var img = new Image();
    img.width = 200;
    img.height = 200;

    img.onload = function(){
      var imgInstance = new fabric.Image(img);
      canvas.add(imgInstance);
      canvas.moveTo(imgInstance, 100);
    };
    img.src = event.target.result;
  }
  reader.readAsDataURL(e.target.files[0]);
}


var imageSaver = document.getElementById('imageSaver');
imageSaver.addEventListener('click', saveImage, false);

function saveImage(e){
  this.href = canvas.toDataURL({
    format: 'jpeg',
    quality: 0.8
  });
  this.download = 'test.png';
  console.info(this.href);
}


var App = React.createClass({
  render() {
    return (<div>
      <Operation />
    </div>);
  }
});

ReactDOM.render(<App/>, document.getElementById('react-container'));




